import { auth } from "@/auth";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import Image from "next/image";
import active_icon from "@/public/active.png";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { BadgeCheckIcon } from "lucide-react";

export default async function Profile() {
  const session = await auth();
  const user = session?.user;
  if (!user) return null;
  console.log("User", user);
  const { email, first_name, last_name, is_active, is_verified, roles } = user;
  return (
    <>
      <div className="border border-[#eeeeee] max-w-[388px] w-full ml-[122px] mt-[160px] mb-16 p-6 rounded-[4px]">
        <div className="">
          {/* avatar */}
          <div className="relative">
            <Avatar className="w-[64px] h-[64px]">
              <AvatarImage src={"/avatar.png"} />
              <AvatarFallback>AB</AvatarFallback>
            </Avatar>
            {is_active ? (
              <Image
                className="absolute bottom-[-4px] left-10 "
                width={16}
                height={16}
                src={active_icon}
                alt="active"
              />
            ) : (
              ""
            )}
          </div>
          {/* username and email */}
          <div className="mt-4 flex justify-between">
            <div className="">
              {" "}
              <h1 className="mb-0 text-lg font-bold text-brand-black">
                {first_name}
              </h1>
              <p className="text-brand-gray"> {email} </p>
            </div>
            <div className=" flex items-center">
              {" "}
              {/* <span className="bg-brand text-brand-primary text-[16px] rounded-full py-1 px-4">
                {is_verified ? "Verivied" : "not varivied"}{" "}
              </span> */}
              <Badge
                variant="secondary"
                className={`text-brand-primary ${
                  is_verified
                    ? "bg-blue-500  dark:bg-blue-600"
                    : "bg-red-500 dark:bg-red-600"
                }`}
              >
                <BadgeCheckIcon />
                Verified
              </Badge>
            </div>
          </div>
        </div>
        <h1 className="text-2xl font-bold text-brand-black my-8">
          Personal Information
        </h1>
        <div className="">
          {/* firstname */}
          <div className="">
            <Label className="text-[16px] text-brand-gray font-medium">
              First Name
            </Label>
            <h2 className="text-lg text-brand-black font-medium mt-[-8px]">
              {" "}
              {first_name}{" "}
            </h2>
          </div>
          {/* lastname */}
          <div className="mt-4">
            <Label className="text-[16px] text-brand-gray font-medium">
              Last Name
            </Label>
            <h2 className="text-lg text-brand-black font-medium mt-[-8px]">
              {" "}
              {last_name}{" "}
            </h2>
          </div>
          {/* email */}
          <div className="mt-4">
            <Label className="text-[16px] text-brand-gray font-medium">
              Email
            </Label>
            <h2 className="text-lg text-brand-black font-medium mt-[-8px]">
              {" "}
              {email}{" "}
            </h2>
          </div>
          {/* role */}
          <div className="mt-4">
            <Label className="text-[16px] text-brand-gray font-medium">
              Role
            </Label>
            <h2 className="text-lg text-brand-black font-medium mt-[-8px]">
              {" "}
              {roles[0]}{" "}
            </h2>
          </div>
        </div>
        <Button className="mt-8 text-lg text-brand-primary py-6 hover:bg-brand/90  font-bold max-w-[180px] w-full">
          Change Password
        </Button>
      </div>
    </>
  );
}
